export default {
  name: "Billboard",
  data() {
    return {
      oBillboardIndex: this.$route.params.type || 'recommend',
      oBillboardList: {
        recommend: {text: 'Billboard01', icon: 'far fa-lightbulb'},
        unconfirmed: {text: 'Billboard02', icon: 'far fa-calendar'},
        unanswered: {text: 'Billboard04', icon: 'far fa-comment'},
        confirmed: {text: 'Billboard03', icon: 'far fa-calendar-check'},
      },
    }
  },
  mounted() {
    // console.log(this.$route.params);
    // this.oBillboardNav = this.option1[0]
  },
  methods: {
    handleSelect(v, i) {
      // console.log(i);
      this.oBillboardIndex = i
      this.$refs.BillboardNav.toggle()
      if (this.$route.path !== `/billboard/${i}`) {
        this.$router.replace(`/billboard/${i}`)
      }
    }
  },
}
